.App {
  text-align: center;
}

.App-header {
  background-color: #1a202c; /* より深い背景色 */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
}

/* レスポンシブ対応 */
@media (max-width: 768px) {
  .App-header {
    padding: 15px;
    font-size: calc(8px + 2vmin);
  }
}

@media (max-width: 480px) {
  .App-header {
    padding: 10px;
    font-size: calc(7px + 2vmin);
  }
}

h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #61dafb;
  text-shadow: 0 0 10px rgba(97, 218, 251, 0.5);
}

@media (max-width: 768px) {
  h1 {
    font-size: 1.5rem;
  }
}

.game-message {
  font-size: 1.2rem;
  margin: 15px 0;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 15px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  transition: background-color 0.5s ease;
  font-weight: bold;
}

/* 状態に応じたメッセージスタイル */
.App[data-game-state="observing"] .game-message {
  background-color: rgba(72, 187, 120, 0.3); /* 緑系の背景 */
  border: 1px solid rgba(72, 187, 120, 0.5);
}

.App[data-game-state="changing"] .game-message {
  background-color: rgba(237, 137, 54, 0.3); /* オレンジ系の背景 */
  border: 1px solid rgba(237, 137, 54, 0.5);
  animation: pulse-message 2s infinite;
}

.App[data-game-state="guessing"] .game-message {
  background-color: rgba(66, 153, 225, 0.3); /* 青系の背景 */
  border: 1px solid rgba(66, 153, 225, 0.5);
}

.App[data-game-state="result"] .game-message {
  background-color: rgba(159, 122, 234, 0.3); /* 紫系の背景 */
  border: 1px solid rgba(159, 122, 234, 0.5);
}

@keyframes pulse-message {
  0% {
    box-shadow: 0 0 5px rgba(237, 137, 54, 0.3);
  }
  50% {
    box-shadow: 0 0 15px rgba(237, 137, 54, 0.5);
  }
  100% {
    box-shadow: 0 0 5px rgba(237, 137, 54, 0.3);
  }
}

@media (max-width: 768px) {
  .game-message {
    font-size: 1rem;
    height: auto;
    min-height: 60px;
    padding: 10px 15px;
  }
}

.game-board {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 800px;
  margin: 15px auto;
  gap: 20px;
}

@media (max-width: 768px) {
  .game-board {
    gap: 15px;
    max-width: 600px;
  }
}

@media (max-width: 480px) {
  .game-board {
    gap: 10px;
    max-width: 400px;
  }
}

/* オブジェクトコンテナ（番号表示のため） */
.object-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 25px; /* 番号表示のスペース確保 */
}

.object-number {
  position: absolute;
  bottom: -20px;
  font-size: 0.8rem;
  color: #aaa;
  font-weight: bold;
}

@media (max-width: 480px) {
  .object-number {
    font-size: 0.7rem;
    bottom: -16px;
  }
}

/* 色変化の比較表示 */
.comparison-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px 0;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.comparison-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.comparison-color {
  width: 60px;
  height: 60px;
  margin: 0 15px;
  transition: all 0.3s ease;
}

@media (max-width: 480px) {
  .comparison-color {
    width: 40px;
    height: 40px;
    margin: 0 10px;
  }
}

.comparison-color.circle {
  border-radius: 50%;
}

.comparison-color.square {
  border-radius: 10px;
}

.comparison-label {
  margin-top: 8px;
  font-size: 0.8rem;
  color: #ddd;
}

.comparison-arrow {
  font-size: 1.5rem;
  margin: 0 10px;
  color: #61dafb;
}

@media (max-width: 480px) {
  .comparison-arrow {
    font-size: 1.2rem;
    margin: 0 5px;
  }
}

/* プログレスバー */
.progress-container {
  width: 80%;
  max-width: 600px;
  margin: 15px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress-label {
  font-size: 0.9rem;
  color: #aaa;
  margin-bottom: 5px;
  font-weight: bold;
}

.progress-bar {
  width: 100%;
  height: 10px;
  background-color: #48bb78;
  border-radius: 5px;
  transition: width 0.05s linear, background-color 0.5s ease;
  box-shadow: 0 0 5px rgba(72, 187, 120, 0.5);
}

/* 遊び方の説明スタイル */
.game-instructions {
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  padding: 20px;
  margin: 15px 0;
  max-width: 600px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
}

@media (max-width: 768px) {
  .game-instructions {
    padding: 15px;
  }
}

.game-instructions h2 {
  color: #61dafb;
  margin-top: 0;
}

.game-instructions ol {
  text-align: left;
  padding-left: 25px;
}

.game-instructions li {
  margin: 10px 0;
  font-size: 1rem;
}

@media (max-width: 768px) {
  .game-instructions li {
    font-size: 0.9rem;
    margin: 8px 0;
  }
}

.hide-instructions, .show-instructions {
  background-color: #61dafb;
  color: #282c34;
  border: none;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  margin: 10px 0;
  transition: all 0.2s ease;
  font-weight: bold;
}

.hide-instructions:hover, .show-instructions:hover {
  background-color: #4fa8d1;
  transform: scale(1.05);
}

.hide-instructions:focus, .show-instructions:focus {
  outline: 2px solid white;
}

/* 難易度説明 */
.level-description {
  font-size: 0.9rem;
  color: #aaa;
  margin-top: 20px;
  font-style: italic;
  text-align: center;
  max-width: 600px;
}

@media (max-width: 768px) {
  .level-description {
    font-size: 0.8rem;
  }
}

/* 結果画面のコントロール */
.result-controls {
  display: flex;
  gap: 10px;
  margin: 15px 0;
  flex-wrap: wrap;
  justify-content: center;
}

@media (max-width: 480px) {
  .result-controls {
    flex-direction: column;
    gap: 8px;
  }
}

.result-controls button {
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: bold;
  transition: all 0.2s ease;
}

.result-controls button:hover {
  transform: scale(1.05);
}

.result-controls button:focus {
  outline: 2px solid white;
}

.compare-button {
  background-color: #805ad5;
  color: white;
}

.compare-button:hover {
  background-color: #6b46c1;
}

.retry-button {
  background-color: #38a169;
  color: white;
}

.retry-button:hover {
  background-color: #2f855a;
}