.game-controls {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
  max-width: 600px;
}

@media (max-width: 768px) {
  .game-controls {
    margin-top: 20px;
    gap: 15px;
  }
}

.level-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
}

.level-controls h3 {
  margin-bottom: 10px;
  color: #61dafb;
}

.level-buttons {
  display: flex;
  gap: 10px;
}

@media (max-width: 480px) {
  .level-buttons {
    gap: 5px;
  }
}

.level-buttons button {
  padding: 8px 15px;
  font-size: 0.9rem;
  border: none;
  border-radius: 5px;
  background-color: #4a5568;
  color: white;
  cursor: pointer;
  transition: all 0.2s ease;
}

@media (max-width: 480px) {
  .level-buttons button {
    padding: 6px 12px;
    font-size: 0.8rem;
  }
}

.level-buttons button:hover:not(:disabled) {
  background-color: #2d3748;
  transform: scale(1.05);
}

.level-buttons button:focus:not(:disabled) {
  outline: 2px solid white;
}

.level-buttons button.active {
  background-color: #4299e1;
  box-shadow: 0 0 10px #4299e1;
  transform: scale(1.05);
}

.level-buttons button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.game-buttons {
  display: flex;
  gap: 15px;
  margin-top: 10px;
}

@media (max-width: 480px) {
  .game-buttons {
    gap: 10px;
  }
}

.start-button, .reset-button {
  padding: 10px 25px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: bold;
}

@media (max-width: 480px) {
  .start-button, .reset-button {
    padding: 8px 20px;
    font-size: 0.9rem;
  }
}

.start-button {
  background-color: #48bb78;
  color: white;
  box-shadow: 0 0 8px rgba(72, 187, 120, 0.5);
}

.start-button:hover {
  background-color: #38a169;
  transform: scale(1.05);
}

.start-button:focus {
  outline: 2px solid white;
}

.reset-button {
  background-color: #f56565;
  color: white;
  box-shadow: 0 0 8px rgba(245, 101, 101, 0.5);
}

.reset-button:hover {
  background-color: #e53e3e;
  transform: scale(1.05);
}

.reset-button:focus {
  outline: 2px solid white;
}

.score-display {
  font-size: 1.2rem;
  margin-bottom: 10px;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 10px 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.score-display h3 {
  margin: 0;
  color: #61dafb;
}

@media (max-width: 480px) {
  .score-display {
    font-size: 1rem;
    padding: 8px 15px;
  }
}