.color-object {
  width: 80px;
  height: 80px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 3px solid transparent;
  position: relative;
}

.color-object:hover {
  transform: scale(1.05);
}

.color-object.circle {
  border-radius: 50%;
}

.color-object.square {
  border-radius: 10px;
}

.color-object.selected {
  border-color: white;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.7);
}

.color-object.correct {
  border-color: gold;
  box-shadow: 0 0 15px gold;
  animation: pulse 1s infinite;
}

/* 回転するアニメーション（注意を散らすため） */
.color-object.rotating {
  animation: rotate 3s infinite linear;
}

/* 点滅するアニメーション（注意を散らすため） */
.color-object.pulsing {
  animation: scalePulse 2s infinite alternate;
}

/* アニメーションが複数ある場合 */
.color-object.rotating.pulsing {
  animation: rotate 3s infinite linear, scalePulse 2s infinite alternate;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 10px gold;
  }
  50% {
    box-shadow: 0 0 20px gold;
  }
  100% {
    box-shadow: 0 0 10px gold;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes scalePulse {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1.1);
  }
}
/* 色変化アニメーション */
.color-transition {
  transition: background-color 1.5s ease;
}

.color-animating {
  animation: colorChange var(--animation-duration) ease forwards;
}

@keyframes colorChange {
  0% {
    background-color: var(--start-color);
  }
  100% {
    background-color: var(--end-color);
  }
}

/* アニメーションと他の効果を組み合わせた場合 */
.color-object.rotating.color-animating {
  animation: 
    rotate 3s infinite linear,
    colorChange var(--animation-duration) ease forwards;
}

.color-object.pulsing.color-animating {
  animation: 
    scalePulse 2s infinite alternate,
    colorChange var(--animation-duration) ease forwards;
}